// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueRouter from 'Vue-Router'
import VueResource from 'vue-resource'
import detail from './components/detail'
import tongji from './components/tongji'
import yuce from './components/yuce'
import fenxi from './components/fenxi'
import fabu from './components/fabu'


Vue.use(VueRouter)
Vue.use(VueResource)

Vue.config.productionTip = false

const router=new VueRouter({
	mode:'history',
	base:__dirname,
	routes:[
	{path:'/',component:App},
	{path:'/detail',component:detail,
	redirect:'/detail/tongji',
	children:[
	{path:'tongji',component:tongji},
	{path:'yuce',component:yuce},
	{path:'fenxi',component:fenxi},
	{path:'fabu',component:fabu},

	]
}
	]
	
})

/* eslint-disable no-new */
new Vue({
  router,

  // router,
 template:`
	<div id="app">
		
		<router-view></router-view>

	</div>
 `
}).$mount('#app')



