<template>
  <div class="left">
	  	<div class="wrap">
	    	<div class='top'>
	    		<h4>全部产品</h4>
	    	</div>

	    	<div class="mid">
	    		<template v-for='firstItem in firstList' >
	    			<h3>{{firstItem.title}}</h3>
	    			<ul>
	    				<li v-for="item in firstItem.list"><a :href="item.url">{{item.name}}<span v-show="item.hot">hot</span></a></li>
	    			</ul>

	    		</template>
	    		
	    	</div>
	    
	   <div class='top'>
	   	<h4>最新消息</h4>
	   </div>
	   <div>
	    			<ul >
	    				<li v-for='item in newlist'><a :href="item.url" class="aclass">{{item.title}}</a></li>
	    			</ul>
	    		</div>
	   </div>
	   <div style="height:100px;"></div>
	   <div class="midmid">
	   		<div v-for='(item,index) in  phtotList' class="pad">
	   			<div class="img" :class="[{mar:index%2!==0},'index'+index]" ></div>
	   			<ul class="ulindex">
	   					<li><a :href="item.url">{{item.title}}</a></li>
	   					<li>{{item.introduce}}</li>
	   					<li><button>立即购买</button></li>
	   				</ul>	
	   		</div>

	   </div>
  </div>
</template>

<script>
export default {
  name: 'left',
  data () {
    return {
    	newlist:[],
    	firstList:[
    	{
    		title:'PC产品',
    		list:[
    			{name:'数据统计',url:'#'},
    			{name:'数据预测',url:'#'},
    			{name:'流量分析',url:'#',hot:true},
    			{name:'广告发布',url:'#'}
    		],
    },{
    		title:'手机应用类',
    		list:[
    			{name:'91助手',url:'#'},
    			{name:'产品助手',url:'#',hot:true},
    			{name:'智能地图',url:'#'},
    			{name:'团队语音',url:'#'}
    		]


    }		
    	],
    	phtotList:[
    		{title:'开发产品',introduce:'此产品是一种开发产品',url:'#',photoUrl:'../assets/images/1.png'},
    		{title:'品牌营销',introduce:'名牌是品质的保证，是大众的信任。',url:'#',photoUrl:'../assets/images/2.png'},
    		{title:'使命必达',introduce:'我们会安全又迅速的把产品送到你的手里.',url:'#',photoUrl:'../assets/images/3.png'},
    		{title:'勇攀高峰',introduce:'这是我们品牌的一种追求和目标。',url:'#',photoUrl:'../assets/images/4.png'}
    	]
    }
  },
  created(){
  	this.$http.get('http://localhost:3000/getNewsList')
  	.then((reponse)=>{
  		console.log(reponse)
  		this.newlist=reponse.body
  	},(err)=>{
  		console.log(err)
  		alert('页面找不到：404')
  	})

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{padding: 0;
margin:0;}
.wrap{
	width:260px;
	margin-left:30px;
	background: #F4F4F4;
	padding-top: 10px;
	
}
.top ul{
	margin-top: 10px;
	padding:10px 0px 0px 10px;
}
.top{

	background: lightgreen;
	width: 100%;
	height: 30px;
	color:white;
	line-height: 30px;
	padding-left: 15px;
	box-sizing: border-box;
}
.wrap li{
	list-style-type: none;
	margin-bottom: 8px;
}
 li a {
	text-decoration: none;
	color:black;
}
span{
	background: red;
	color:white;
	padding: 2px;
	margin-left:4px;
}

.midmid{
	width: 990px;
	/*background: green;*/
	position: absolute;
	left: 350px;
	top:380px;

}

.img{
	width: 100px;
	height: 100px;
/*	display: inline-block;*/
	/*border:1px solid ;*/
	box-sizing: border-box;
	float: left;
	margin-top: 50px;
	

}

.midmid ul{
	display: inline-block;
	height: 100px;
	width: 300px;
	float: left;
	list-style-type: none;
	margin-top: 50px;

}
.midmid div:nth-child(3){
	
	background: red;
}
/*.midmid ul:nth-child(1){
	margin-top: 0px;
}*/
.midmid ul li{
	margin-top: 5px;
}

.mar{
	margin-left:100px ; 
}
.pad{
	padding-left: 40px;
}
.midmid button{
	border:none;
	background: lightgreen;
	color: white;
	padding: 2px;
}
.index0{
	background: url('../assets/images/1.png') center no-repeat;

}
.index1{
	background: url('../assets/images/2.png') center no-repeat;

}
.index2{
	background: url('../assets/images/3.png') center no-repeat;
	
}
.index3{
	background: url('../assets/images/4.png') center no-repeat;
	
}
.aclass{
	display: inline-block;
	width: 80px;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}
</style>



