<template>
  <div class="detail">
	<myheader></myheader>
	<div class="mid">
		<template>
			<img :src="addphoto" alt="">
			<ul>
				<li v-for="item in list"><router-link :to="item.url" >{{item.name}}</router-link></li>
			</ul>
		</template>
		<keep-alive>
		<router-view></router-view>
		</keep-alive>

    <mybottom></mybottom>
	</div>	
	
  </div>
</template>
<script>
import myheader from './header'
import mybottom from './bottom'

export default {
  name: 'detail',
  components:{
  	'myheader':myheader,
  	'mybottom':mybottom
  },
  data () {
    return {
    		list:[
    			{name:'数据统计',url:'/detail/tongji'},
    			{name:'数据预测',url:'/detail/yuce'},
    			{name:'流量分析',url:'/detail/fenxi',hot:true},
    			{name:'广告发布',url:'/detail/fabu'}
    		],
    		addimg:
    			{
    			'/detail/tongji':require("../assets/images/1.png"),
    			'/detail/yuce':require("../assets/images/2.png"),
    			'/detail/fenxi':require("../assets/images/3.png"),
    			'/detail/fabu':require("../assets/images/4.png")
    		}
    		    	
  }
},
computed:{
	addphoto:function(){

		console.log(this.$route.path)
		 return this.addimg[this.$route.path]
		
	}
}

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.detail li{
  list-style-type: none;
  margin-bottom: 5px;
}
.detail a{
  text-decoration: none;
  

}
.router-link-active{
transform: scale(2);
color: white;
background: green;

}
</style>



