<template>
  <div class="bankImg" v-if="">
    <div v-for="(item,index) in banksList" @click="clickIndex(index)" :class="{active:nowIndex==index}"></div>
    
    <div>
	<button @click="subBank">确认购买</button>
	</div>
  </div>

</template>

<script>
export default {
	props:{
		banksList:{
			type:Array,
		}
	},
  data () {
    return {
      	nowIndex:0,
    }
  },
  methods:{
  	 clickIndex:function(index){
  		this.nowIndex=index

  		  },
  		subBank:function(){
        this.$emit('on-close')
  			 console.log(this.banksList[this.nowIndex].bankId)

         this.$http.post('http://localhost:3000/checkBank',(this.banksList[this.nowIndex]))
         .then((res)=>{
            console.log(res)
         })
  		}  
  }
 

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.active{
	border:2px groove red;
}
.bankImg{
	width:500px;
	margin:0 auto;
	margin-left: 18px;
	
}
.bankImg div{
	width: 120px;
	height: 35px;
	box-sizing: border-box;
}
.bankImg div:nth-child(1){
	
   background: url('../assets/banks/banks.png');
   float:left;
}
.bankImg div:nth-child(2){
	
   background: url('../assets/banks/banks.png') 120px;
   float:left;
}
.bankImg div:nth-child(3){
	
   background: url('../assets/banks/banks.png') 243px;
   float:left;
}
.bankImg div:nth-child(4){
	
	height: 35px;
   background: url('../assets/banks/banks.png') 368px;
   float:left;
}
.bankImg div:nth-child(5){
	
   background: url('../assets/banks/banks.png') 480px;
   float:left;
}
.bankImg div:nth-child(6){
	
   background: url('../assets/banks/banks.png') 600px;
   float:left;
}
button{
	border:none;
	background: green;
	color:white;
	width: 100px;
	margin:0px auto;
}
</style>



