<template>
  <div class="slideShow" @mouseover="over" @mouseout="out">
    <div class="photo">
			
		<router-link :to="slidePhtop[indexNum].href">
			<transition name="fade-old" mode="out-in">
			<img :src="slidePhtop[indexNum].src" alt="" v-if="isshow" class="imgclass1">
		</transition>
 
			<transition name="fade-new"  mode="out-in">
			<img :src="slidePhtop[indexNum].src" alt="" v-if="!isshow" class="imgclass1">
		</transition>
		</router-link>
	</div>
	<div class="listNum">
			<span class="spamMar ">{{text}}</span>
		<ul>
		
			<li @click.prevent="onchange(back)"><</li>
			<li v-for="(item,index) in slidePhtop" @click="onchange(index)" :class="{on:index==indexNum}"> </span>	{{item.id}}</li>
			<li @click.prevent="onchange(next)">></li>
		</ul>
	</div>
		
  </div>
</template>

<script>
export default {
  name: 'slideShow',
  props:['slidePhtop'],
  data () {
    return {
      indexNum:"0",
      text:"one",
      timeId:null,
      spendTime:4000,
      isshow:true,
      
    }
  },
  methods:{
  	onchange(index){
  		this.isshow=false
  		setTimeout(()=>{
  			this.isshow=true
  			this.indexNum=index
  			
  		},10)
  		
  	},
  	// over:function(){
  	// 	setTimeout(timeId)
  	// },
  	over:function(){
  		clearInterval(this.timeId)
  		
  	},
  	out:function(){
  		this.setInt()
  	},
  	setInt(){
  		this.timeId=setInterval(()=>{
  		this.onchange(this.next)
  	},this.spendTime)
  	}
  	
  },
  created(){
  	this.setInt()
  },
  watch:{
  	indexNum:{
  		// this.text=this.slidePhtop[indexNum].title
  		handler:function(){
  			// alert(this.indexNum)
  			this.text=this.slidePhtop[this.indexNum].title
  			this.href=this.slidePhtop[this.indexNum].href
  		},
  		deep:true
  	}
  },
  computed:{
  	next(){
  		if(this.indexNum==this.slidePhtop.length-1){
  			return 0
  		}else{
  			return parseInt(this.indexNum)+1
  		}

  	},
  	back(){
  		if(this.indexNum==0){
  			return this.slidePhtop.length-1
  		}else{
  			return this.indexNum-1
  		}
  	}
  },
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
*{
	padding: 0;
	margin: 0;
}
.slideShow{
	height: 300px;
	width: 850px;
	position: absolute;
	top:110px;
	left:400px;
	overflow: hidden;
}
.photo{
	width: 100%;
	height: 90%;
	position: absolute;
	top:0px;
	left:0px;


}
.spamMar{
	colro:white;
	position: absolute;
	left: 5px;
	top:91%;
	color: white;
}
.photo img{
	height: 100%;
	width: 100%;
	position: absolute;
}
.listNum{
	width: 100%;
	height: 10%;
	background: black;
	margin-top: 270px;
	
}
.listNum ul{
	margin-left:83%;
}
.listNum li{
	display: inline-block;
	list-style-type: none;
	cursor: pointer;
	
	margin: 5px 0 0 10px;
	color: white;
}
.on{
	padding: 2px;
	background: lightgreen;
}
/*donghu*/
.fade-old-enter-active{
	transform: translateX(0px);
	transition:all  1.5s linear;
	
}
.fade-old-enter{
	transform: translateX(800px);
}
.fade-new-leave-active{
	transition: all 1.5s linear;
	transform: translateX(-800px);
}
.imgclass1{
	display: block;
}
</style>



