<template>
  <div class="sign">
  	<div>
      <label><h3>登录界面</h3></label> 
    	<form @submit.prevent="onsubmit()">

    		<input type="button" value="用  户  名"><input type="email" v-model='emailText' autocomplete="on"><br>
    		<input type="button" value="登录密码"><input type="password" v-model='passText'><br>
    		<input type="submit" value="登录">
    	</form>
	</div>

  </div>
</template>

<script>
export default {
  name: 'sign',
  props:{
    keep:{
      type:String,
    }
  },
  // props:{
  // 	lishow:{
  // 		type:Boolean,
  // 		default:false
  // 	},
  // 	lishow1:{
  // 		type:Boolean,
  // 		default:false
  // 	}
  // },
  data () {
    return {
      	emailText:'',
      	passText:''
    }
  },
created(){
    this.emailText=this.keep
  },
  methods:{
  	onsubmit:function(){
  		console.log(this.emailText,this.passText)
  		this.$http.get('http://localhost:3000/login')
  		.then((data)=>{
  			console.log(data)
  			let bool=false
  			for(let i=0;i<data.body.length;i++){
  				if(this.emailText==data.body[i].username && this.passText==data.body[i].userId){
  				alert('登录成功')
  				return this.$emit('signChang',[this.emailText,true,false,this.emailText])
  				bool=true		
  			}
}
if(bool){
  		bool=false
  			}else{
  				alert('密码或账号错误')
  	}
  			
  		},(err)=>{

  		})
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.sign div{
	padding: 17px;
  background: #D7D0D0;

}
.sign div input:nth-child(3n-2){
  background: blue;
  color:white;
}
input {
  margin:2px 0 0 4px;
}
h3{

  margin:2px 0 5px 80px;
}
</style>



