<template>
  <div class="content">
    <left></left>
	<slideShow :slidePhtop="slidePhtop"></slideShow>

  </div>
</template>
<script>
import slideShow from './slideShow'
import left from './left'
export default {
  name: 'mid',
  data () {
    return {
       slidePhtop:[
      		 {id:1,title:"one",src:require('../assets/slideShow/pic1.jpg'),href:'/detail/tongji'},
      		 {id:2,title:"two",src:require('../assets/slideShow/pic2.jpg'),href:'/detail/yuce'},
      		 {id:3,title:"three",src:require('../assets/slideShow/pic3.jpg'),href:'/detail/fenxi'},
      		 {id:4,title:"four",src:require('../assets/slideShow/pic4.jpg'),href:'/detail/fabu'}
       ]
    }
  },
  components:{
  	"left":left,
  	"slideShow":slideShow
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



