<template>
  <div class="myDialong"  >
  	<transition name="fade">
 <div class="hide" @click='closeMyself()' v-if="isshow"></div>
 	</transition>
 	<transition name="fade">
  <div class="first" v-if="isshow">
  	<span><button @click='closeMyself()'>X</button></span>
  	<slot>empty</slot>	
  </div>
</transition>
   

  </div>
</template>

<script>
export default {
  name: 'myDialong',
  props:{
  	isshow:{
  		type:Boolean,
  		default:false
  	}
  	
  },
  data () {
    return {
      	
    }
  },
  methods:{
  	closeMyself:function(){
  		
  		this.$emit('on-close')
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hide{
	width: 100%;
	height: 100%;
	/*background: red;*/
	position: absolute;
	top:0;
	left:0;
	z-index: 1;

}

.first{
	background: #D7D0D0;
	position: fixed;
	top:30%;
	left: 40%;
	z-index: 10;
	word-wrap: break-word;
	color:black;

}
span button{
	border: none;
	background: #D7D0D0;
	color:red;
	margin-left: 82%;
	font-size: 30px;
}
.fade-enter-active,.fade-leave-active{
	transition: all 0.5s ease-out;
}
.fade-enter,.fade-leave-to{
	transform: translateY(-500px);
}
</style>



