<template>
  <div class="header">

  	<router-link to='/'><img src="../assets/logo.png" alt=""></router-link>
    <ul>
    	<li  @click="showSign()"  v-if='!liShow'>{{sign}}</li>
    	<li  v-if='liShow' ><span >{{sign}}</span></li>
    	<li>|</li>
    	<li   @click="showRegister()" v-if='!liShow'>{{registerText}}</li>
    	<li   @click="signOut" v-if='liShow'>{{registerText}}</li>
    	<li>|</li>
    	<li  @click="showAbout()" >关于</li>
    </ul>

	 <myDialong  :isshow="signDialong" @on-close="newchange('signDialong')" >
	 		
     	<sign   @signChang="changSign" :keep="keepEmail"></sign>
  
    </myDialong>
		
    <myDialong :isshow="registerDialong" @on-close="newchange('registerDialong')">
      <p>register</p>
    </myDialong>
	
    <myDialong :isshow="abdoutDialong" @on-close="newchange('abdoutDialong')">
      <pre>
      	      aboutaboutaboutaboutaboutaboutaboutab
	       outaboutaboutaboutaboutaboutaboutabou
	       taboutaboutaboutaboutaboutaboutabouta
	       boutaboutabtaboutaboutaboutaboutabout
	       aboutaboutaboutaboutabout 
		</pre>
    </myDialong>
  </div>
</template>

<script>
import myDialong from  './dialong'
import sign from './sign'
export default {
  name: 'top',
  components:{
  	'myDialong':myDialong,
  	sign

  },
  data () {
    return {
      abdoutDialong:false,
      signDialong:false,
      registerDialong:false,
      sign:'登录',
      registerText:'注册',
      liShow:false,
      keepEmail:'linxiaopang@qq.com'

    }
  },
  methods:{
  	showSign:function(){
  		this.signDialong=true
  	},
  	showRegister:function(){
  		this.registerDialong=true
  	},
  	showAbout:function(){
  		this.abdoutDialong=true
  	},
  	newchange:function(attr){
  		this[attr]=false
  	},
  	changSign:function(value){
  		console.log(value)
  		this.sign=value[0]
  		this.liShow=value[1]
  		this.signDialong=value[2]
  		this.registerText="退出"
      this.keepEmail=value[3]
  	},
  	signOut:function(){
  		this.sign="登录"
  		this.registerText='注册'
  		this.liShow=false	
  	},

  

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header{
	width: 100%;
	height: 80px;
	background: black;
	color:white;

}
span{
	text-overflow: ellipsis;
	display: inline-block;
	width: 80px;
	overflow: hidden;
}
ul{
	overflow: hidden;
	margin-left: 75%;
}
li{
	float: left;
	cursor: pointer;
	list-style-type: none;
	margin-right: 15px;
	line-height: 80px;

}

img{
	height: 60%;
	margin:20px 0px 0px 30px;
	float: left;
}
/*donghua*/

</style>



