<template>
  <div class="fenxi">
    <div >
        <h2>流量分析</h2>
        <p>是指在获得网站访问量基本数据的情况下对有关数据进行统计、分析，从中发现用户访问网站的规律，并将这些规律与网络营销策略等相结合，从而发现目前网络营销活动中可能存在的问题，并为进一步修正或重新制定网络营销策略提供依据。当然这样的定义是站在网络营销管理的角度来考虑的</p>
      </div>
      <div class="purchase">
          <div>
            <h3>购买数量：</h3> <button @click="total.number++">+</button><span>{{total.number}}</span><button @click="total.number--" v-show="total.number-1">-</button>
          </div>
          <div>
            <h3>产品类型：</h3>
            <select v-model='total.useType'>
                <option value="1" >低级版</option>
                  <option value="2" selected="" >中级版</option>
                  <option value="3" >高级版</option>  
            </select>
          </div>
          <div>
            <h3>有效时间：</h3> 
            <input type="radio" name='year' value="0.5" v-model="total.usetime"> <span>半年</span>
            <input type="radio" name='year' value="1" checked="" v-model="total.usetime"><span>一年</span>
            <input type="radio" name='year' value="2.5" v-model="total.usetime"> <span>三年</span>
          </div>
          <div>
            <h3>产品版本：</h3>
           <input type="radio" name="edition" value="客户版" v-model="total.editionValue">客户版</span>
            <input type="radio" name="edition" value="代理商版" v-model="total.editionValue"> <span>代理商版</span>
            <input type="radio" name="edition" value="专家版" v-model="total.editionValue"> <span>专家版</span>
          </div>
          <div>
            <h3>总价：</h3> <span>{{price}}元</span>
          </div>
          <div><button @click='sumTotal()'>立即购买</button></div>
          <myDialong :isshow="isShow"  @on-close="hideDialong()">
            
          </myDialong>
          <bank :list="newList" :isshow='isNewShow' @on-close='newChange'></bank>
          <childBank :isChlidShow='isChlidShow' @on-close='childBankHide'></childBank>
      </div>



  </div>
</template>

<script>
import myDialong from './dialong'
import childBank from './childBnak'
import bank from './bank'
export default {
  components:{
    'myDialong':myDialong,
    'Bank':bank,
    'childBank':childBank
  },
  data () {
    return {
        isShow:false,
        isNewShow:false,
        isChlidShow:false,
        total:{
          number:1,
          editionValue:'客户版',
           useType:1,
           newUseType:null,
          usetime:1,
          newUsetime:null,
        },
        newList:{},
        priceList:[
         {
            "edition": "客户版",
            "price": 500,
            "year": 1
          },
          {
            "edition": "代理商版",
            "price": 1000,
            "year": 1
          },
          {
            "edition": "专家版",
            "price": 1500,
            "year": 1
          }
        ],
        

        
        
    }
  },
  methods:{
    childBankHide:function(){
      this.isChlidShow=false
    },
    newChange:function(){
      this.isNewShow=false
      this.isChlidShow=true
    },
    click:function(){
      alert(this.total.number++)
    },
    sumTotal:function(){
      this.isNewShow=true
      this.total.allMoney=this.price
     if(this.total.usetime==0.5){
     this.total.newUsetime='on half year'
     }else if(this.total.usetime==1){
       this.total.newUsetime='one  year'
     }else{
      this.total.newUsetime='three year'
     }
     if(this.total.useType==1){
      this.total.newUseType='低级版'
     }else if(this.total.useType==2){
      this.total.newUseType='中级版'
     }else{
       this.total.newUseType='高级版'
     }
      // console.log(this.total)
      this.$http.post('http://localhost:3000/moneyList',this.total)
      .then((reponse)=>{
        this.newList=JSON.parse(reponse.bodyText)
        console.log(typeof  this.newList)
      })
    },
    hideDialong:function(){
      this.isShow=false
    }



  },
  computed:{
price: function(){
 if(this.total.editionValue==this.priceList[0].edition){
    return this.total.number*this.priceList[0].price*this.total.usetime*this.total.useType
 }else if(this.total.editionValue==this.priceList[1].edition){
  return this.total.number*this.priceList[1].price*this.total.usetime*this.total.useType
 }else{
   return this.total.number*this.priceList[2].price*this.total.usetime*this.total.useType
 }
}    
  },
  // created(){
  //   this.$http.get('http://localhost:3000/price')
  //   .then((reponse)=>{
  //    console.log(reponse.body[0]['year'])
  //   })
  // }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.fenxi{
  margin-left: 150px;
  margin-top: -200px;
  border:2px groove green;
  padding: 10px;
  box-shadow: 3px 3px 5px red;
  border-radius: 50px;

}
.purchase h3{
  display: inline-block;
}
</style>



