<template>
<transition name='fade'>
  <div class="bank" v-if="isshow">
    <div class="top">
    	<div class="mid">
    	<div><h5>数量</h5><span>{{list.number}}</span></div>
    	<div><h5>产品版本</h5><span>{{list.editionValue}}</span></div>
    	<div><h5>使用时间</h5><span>{{list.newUsetime}}</span></div>
    	<div><h5>产品类型</h5><span>{{list.newUseType}}</span></div>
    	<div><h5>总金额</h5><span>{{list.allMoney}}元</span></div><br>
    	</div> 	
    </div>
		<bankImg :banksList="banksList" @on-close='newchange'></bankImg>
		
  </div>
</transition>
</template>

<script>
import bankImg from './bankImg'
import childBank from './childBnak'
export default {
	components:{
		"bankImg":bankImg,
		'childBank':childBank
	},
	props:{
		isshow:{
			type:Boolean,
			default:false
		},
		list:{
			type:Object,
		}
	},
  data () {
    return {
     	banksList:[
     		{name:'兴业银行',bankId:111},
     		{name:'包商银行',bankId:222},
     		{name:'承德银行',bankId:333},
     		{name:'交通银行',bankId:444},
     		{name:'农村银行',bankId:555},
     		{name:'农商银行',bankId:666},
     	]
    }
  },
  methods:{
  	newchange:function(){
  		this.$emit('on-close')
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bank{
	position: absolute;
	left: 400px;
	top:300px;
	border:2px solid red;
	width: 500px;
	padding: 15px;
}
.mid div{
	display: inline-block;
	text-align: center;
	
}
.mid div h5{
	background: lightgreen;
	padding: 5px;
	width: 60px;
}
.mid div span{
	background: red;
	color:white;
	width: 60px;
	display: inline-block;
	font-size: 12px;
	height: 25px;
	line-height: 25px;
}
.mid{
	width: 450px;
	height: 100px;
	background: green;
	margin:0 auto;
	border:5px groove green;

}
.top{
	width:500px;
	margin:0 auto;
}


.fade-enter-active,.fade-leave-active{
	transition: all 0.5s ease-out;
}
.fade-enter,.fade-leave-to{
	transform: translateY(-500px);
}
</style>



