<template>
	<transition name='fade'>
  <div class="childBank" v-if='isChlidShow'>
    
	<div>
		<button @click="successShow=true">支付成功</button><button @click="failShow=true">支付失败</button>
		<div><h5 v-if="successShow" @click="successHide">支付成功</h5></div>
		<div><h5 v-if="failShow" @click="successHide">支付失败</h5></div>
	</div>

  </div>
</transition>
</template>

<script>
export default {
props:{
	isChlidShow:{
		type:Boolean,
		default:false
	}
},
  data () {
    return {
      successShow:false,
      failShow:false
    }
  },
  methods:{
  	successHide:function(){
  		this.$emit('on-close')
  		this.successShow=false,
      this.failShow=false
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.childBank{
	position: absolute;
	left: 400px;
	top:300px;
	
	
}
.childBank>div{
	background: green;
	width: 300px;
	


}
div button{
	
	border:none;
	background: black;
	color:white;


	
}
div button:nth-child(2){
	margin-left:160px;

}
h5{
	background: black;
	color:white;
	text-align: center;
	padding: 2px;
	text-shadow: 3px 3px 5px red;
	cursor: pointer;
}
.fade-enter-active,.fade-leave-active{
	transition: all 0.5s ease-out;
}
.fade-enter,.fade-leave-to{
	transform: translateY(-500px);
}
</style>



